.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media (max-width: 899px) {
  .window {
    color: white;
    border: solid 3px rgb(124, 121, 121);
    font-size: calc(15px + 2vmin);
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }
}
@media (min-width: 900px) {
  .window {
    color: white;
    border: solid 3px rgb(124, 121, 121);
    font-size: calc(15px + 2vmin);
    width: 50vh;
    height: 60vh;
    margin: 3vh 0vh;
  }
}
.key-grid {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-areas: "display display display display"
                        "clear clear add subtract"
                        "one two three multiply"
                        "four five six divide"
                        "seven eight nine equals"
                        "zero zero decimal equals";
  grid-template-columns: 25%, 25%, 25%, 25%;
  grid-template-rows: 10vh repeat(5, 10vh);
}
Button {
  border-left: solid 1px grey;
  border-top: solid 1px grey;
  border-right: solid 1px black;
  border-bottom: solid 1px black;
  font-size: calc(10px + 2vmin);
  color: white;
}
.numbers {
  background-color: #393b3f;
}
Button:hover {
  border-right: solid 1px grey;
  border-bottom: solid 1px grey;
  border-left: solid 1px black;
  border-top: solid 1px black;
}

.operators {
  background-color: rgb(86, 105, 119);
}

#display {
  overflow: auto;
  background-color: black;
  grid-area: display;
}
#one {
  grid-area: one;
}
#two {
  grid-area: two;
}
#three {
  grid-area: three;
}
#four {
  grid-area: four;
}
#five {
  grid-area: five;
}
#six {
  grid-area: six;
}
#seven {
  grid-area: seven;
}
#eight {
  grid-area: eight;
}
#nine {
  grid-area: nine;
}
#zero {
  grid-area: zero;
}
#add {
  grid-area: add;
}
#subtract {
  grid-area: subtract;
}
#multiply {
  grid-area: multiply;
}
#divide {
  grid-area: divide;
}
#equals {
  grid-area: equals;
  background-color: green;
}
#decimal {
  grid-area: decimal;
}
#clear {
  grid-area: clear;
  background-color: red;
}